import React from 'react';
import ContainerHeader from 'components/ContainerHeader/index';
import IntlMessages from 'util/IntlMessages';
import CardBox from "components/CardBox/index";
import CategoryInfoTable from "./CategoryInfoTable";

const CategoryInfo = (props) => {

    const category_id = props.location.state ? props.location.state.id : null;
    return (
        <div className="app-wrapper">
            <ContainerHeader match={props.match} title={<IntlMessages id="sidebar.stickers.categories"/>}/>
            <div className="row animated slideInUpTiny animation-duration-3">
                <CardBox styleName="col-12">
                    <CategoryInfoTable category_id={category_id}/>
                </CardBox>
            </div>
        </div>
    );
}

export default CategoryInfo;